import React from 'react';
import logo from './logo.svg';
import './App.css';
//import Dashboard from './dashboard/Dashboard'
import Paperbase from './paperbase/Paperbase'
import Content from './paperbase/Content'
import { create } from 'jss';
import rtl from 'jss-rtl';
import browserHistory from './history.js'

import { StylesProvider, jssPreset } from '@material-ui/core/styles';
import {
  Router,
  Switch,
  Route,
  useParams,
  useRouteMatch,
  Redirect
} from "react-router-dom";

import  {
  Main,
  Page1,
  Page2
} from './pages'



import Sample1 from './pdf/samples/Sample1'
import Sample2 from './pdf/samples/Sample2'
import Sample3 from './pdf/samples/Sample3'

import UnitMain from './reports/unit/Main'
import GeneratorMain from './reports/generator/Main'


const jss = create({ plugins: [...jssPreset().plugins, rtl()] });



function App() {
  return (
    <Router history={browserHistory}>
      <StylesProvider jss={jss}>
        <Paperbase>
              <Switch>  
                  <Route exact path="/reportsapp">
                      <Main />
                  </Route>
                  <Route  path="/reportsapp/new/unit">
                    <UnitMain />
                  </Route>
                  <Route path="/reportsapp/new/generator">
                      <GeneratorMain />
                  </Route>

                  <Route path="/reportsapp/samples/sample1">
                      <Sample1 />
                  </Route>
                  
                  <Route path="/reportsapp/samples/sample2">
                      <Sample2 />
                  </Route>

                  <Route path="/reportsapp/samples/sample3">
                      <Sample3 />
                  </Route>
              </Switch>
        </Paperbase>
      </StylesProvider>
    </Router>
  );
}

export default App;
